import axios from 'axios'
// mock.js
import '@/mock'

// 
let API_DOMAIN = 'http://test.com/api/'

// API
export const API_CODE = {
    // API
    OK: 200,
    // API
    ERR_DATA: 403,
    // API
    ERR_NO_DATA: 301,
    // API
    ERR_LOGOUT: 401
}
// API
export const API_FAILED = ''

// API
export const apiReqs = {
    // 
    signIn: (config) => {
        config.url = API_DOMAIN + 'login'
        config.method = 'post'
        fetch(config)
    },
    // 
    getData: (config) => {
        config.url = API_DOMAIN + 'getData'
        config.method = 'get'
        fetch(config)
    }
}

// 
function fetch(config) {
    if (process.env.REACT_APP_DEBUG === 'true') {
        // debug
        apiRequest(config)
    } else {
        // normalbackground script
        sendRequestToBackground(config)
    }
}

/*
 * API
 * config.this: []
 * config.method: []method
 * config.url: []url
 * config.data: 
 * config.formData: formData
 * config.success(res): 
 * config.fail(err): 
 * config.done(): 
 */
export function apiRequest(config) {

    // config.data{}
    if (config.data === undefined) {
        config.data = {}
    }

    // config.methodpost
    config.method = config.method || 'post'

    // Access-Tokenlocalstorage
    let headers = {
        'Access-Token': ''
    }

    let data = null
    // config.formData=truemultipart/form-data
    if (config.formData) {
        headers['Content-Type'] = 'multipart/form-data'
        data = new FormData()
        Object.keys(config.data).forEach(function (key) {
            data.append(key, config.data[key])
        });
    } else {
        data = config.data
    }

    // 
    let axiosConfig = {
        method: config.method,
        url: config.url,
        headers
    }
    if (config.method === 'get') {
        axiosConfig.params = data
    } else {
        axiosConfig.data = data
    }

    // 
    axios(axiosConfig).then((res) => {
        let result = res.data
        // 
        config.done && config.done()
        // 
        config.success && config.success(result)
    }).catch(() => {
        // 
        config.done && config.done()
        // 
        config.fail && config.fail(API_FAILED)

    })
}

// background
function sendRequestToBackground(config) {
    config.apiType = 'background'
    if (window.chrome && window.chrome.runtime) {
        window.chrome && window.chrome.runtime.sendMessage({
            contentRequest: 'apiRequest',
            config: config,
        }, (result) => {
            // background scriptsendResponseresult
            config.done && config.done()
            if (result.result === 'succ') {
                config.success && config.success(result)
            } else {
                config.fail && config.fail(result.msg)
            }
        })
    } else {
        console.log('chrome API')
    }
}

